.table td {
  padding-top: 0.5rem;
  padding-bottom: 0.5rem;
}

.table .title,
.table td {
  padding-left: 1rem;
  padding-right: 1rem;
}

.title {
  cursor: pointer;
  position: relative;
  display: table-cell;
}

.title.active svg {
  position: absolute;
  right: 1rem;
  top: calc(50% - 0.5em);
}

.iconCell {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
.icon {
  height: 3.5rem;
  border-width: 0 0.5rem 0 0 !important;
  border-style: solid;
}
.icon.junk {
  border-color: var(--color-gw2-junk);
}
.icon.basic {
  border-color: var(--color-gw2-basic);
}
.icon.fine {
  border-color: var(--color-gw2-fine);
}
.icon.masterwork {
  border-color: var(--color-gw2-masterwork);
}
.icon.rare {
  border-color: var(--color-gw2-rare);
}
.icon.exotic {
  border-color: var(--color-gw2-exotic);
}
.icon.ascended {
  border-color: var(--color-gw2-ascended);
}
.icon.legendary {
  border-color: var(--color-gw2-legendary);
}

.nameCell {
  padding-top: 0 !important;
  padding-bottom: 0 !important;
}
.name {
}
.name.junk {
  color: var(--color-gw2-junk);
}
.name.basic {
  color: var(--color-gw2-basic);
}
.name.fine {
  color: var(--color-gw2-fine);
}
.name.masterwork {
  color: var(--color-gw2-masterwork);
}
.name.rare {
  color: var(--color-gw2-rare);
}
.name.exotic {
  color: var(--color-gw2-exotic);
}
.name.ascended {
  color: var(--color-gw2-ascended);
}
.name.legendary {
  color: var(--color-gw2-legendary);
}
.description {
  max-width: 50ch;
  text-overflow: ellipsis;
  overflow: hidden;
  white-space: nowrap;
}

.secondary {
  font-size: 0.875rem;
  opacity: 0.75;
}
